
import { defineComponent } from 'vue'
import SwiperCore, { EffectFade, Autoplay } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/vue'
import 'swiper/swiper-bundle.css'

SwiperCore.use([EffectFade, Autoplay])

const DEFAULT_SLIDER_OPTIONS = {
  slidesPerView: 1,
  effect: 'fade',
  pagination: {
    clickable: true
  },
  speed: 2000,
  autoplay: {
    delay: 5000
  },
  loop: true
}

export default defineComponent({
  components: {
    Swiper,
    SwiperSlide
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    options: {
      type: Object,
      required: false,
      default: () => ({})
    }
  },
  data() {
    return {
      swiper: null
    }
  },
  computed: {
    localOptions(): any {
      return {
        ...DEFAULT_SLIDER_OPTIONS,
        ...this.options
      }
    }
  },
  methods: {
    onSwiper(swiper: any) {
      this.swiper = swiper
    }
  }
})
