export const convertMilesToKm = (km: number): number => km * 1.609
export const convertKmToMiles = (km: number): number => km * 0.621
// km
export const DEFAULT_SEARCH_DISTANCE = 50
// hours
export const DEFAULT_SEARCH_DURATION = 2
export const DEFAULT_SEARCH_DATA: ISearch = {
  location: '',
  query: '',
  distance: DEFAULT_SEARCH_DISTANCE,
  duration: DEFAULT_SEARCH_DURATION
}

export const toForm = (data: ISearch) => ({
  ...data,
  // miles
  distance: data.distance && data.distance / 1000,
  // hours
  duration: data.duration && data.duration / 1000 / 60 / 60
})

export const toServer = (data: ISearch) => ({
  ...data,
  // meters
  distance: data.distance ? convertMilesToKm(data.distance) * 1000 : null,
  // ms
  duration: data.duration ? data.duration * 60 * 60 * 1000 : null
})
