<template>
  <div class="hg-carousel">
    <swiper
      class="hg-carousel__inner"
      v-bind="localOptions"
      @swiper="onSwiper">
      <swiper-slide
        v-for="(item, index) in items"
        :key="index"
        class="hg-carousel__item">
        <div
          class="hg-carousel__item-inner"
          :style="{backgroundImage: `url(${item})`}" />
      </swiper-slide>
      <div class="hg-carousel__shadow" />
    </swiper>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import SwiperCore, { EffectFade, Autoplay } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/vue'
import 'swiper/swiper-bundle.css'

SwiperCore.use([EffectFade, Autoplay])

const DEFAULT_SLIDER_OPTIONS = {
  slidesPerView: 1,
  effect: 'fade',
  pagination: {
    clickable: true
  },
  speed: 2000,
  autoplay: {
    delay: 5000
  },
  loop: true
}

export default defineComponent({
  components: {
    Swiper,
    SwiperSlide
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    options: {
      type: Object,
      required: false,
      default: () => ({})
    }
  },
  data() {
    return {
      swiper: null
    }
  },
  computed: {
    localOptions(): any {
      return {
        ...DEFAULT_SLIDER_OPTIONS,
        ...this.options
      }
    }
  },
  methods: {
    onSwiper(swiper: any) {
      this.swiper = swiper
    }
  }
})
</script>

<style lang="scss" scoped>
$color-bg: #7a8279;

@include b(carousel) {
  height: 100%;
  position: relative;
  width: 100%;

  @include e(shadow) {
    background: $color-bg;
    display: block;
    height: 100%;
    left: 0;
    opacity: 0.6;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 2;
  }

  @include e(inner) {
    height: 100%;
    position: relative;
    width: 100%;
    z-index: 1;
  }

  @include e(item-inner) {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
  }
}
</style>
